        <Row class="listBottom" :gutter="8">
            <Col span='9'>
            <div class="panel content">
                <div class="panel-header">
                    <div class="title-text">薪资详细分析</div>
                </div>
                <div class="chart-content">
                    <Row class="topLine">
                        <Col span="24">
                        <!-- 薪资分布箱线图 -->
                        <salaryBox ref="chart11" :data="salaryBoxData"></salaryBox>
                        </Col>
                    </Row>
                    <Row class="bottomPie">
                        <Col span="12">
                        <chart6 ref="chart12" :data="salaryDetail1"></chart6>
                        </Col>
                        <Col span="12">
                        <chart6 ref="chart13" :data="salaryDetail2"></chart6>
                        </Col>
                    </Row>
                </div>
            </div>
            </Col>
            <Col span='4'>
            <div class="panel content">
                <div class="panel-header">
                    <div class="title-text">职位聚类分析</div>
                </div>
                <div class="behavior">
                    <!-- 多维度职位聚类概览图 -->
                    <jobClustering ref="chart14" :data="jobClusterData"></jobClustering>
                </div>
            </div>
            </Col>
            <Col span='4'>
            <div class="panel content">
                <div class="panel-header">
                    <div class="title-text">职位需求分析</div>
                </div>
                <!-- 职位需求漏斗图 -->
                <jobFunnel ref="chart16" :data="jobDemandData"></jobFunnel>
            </div>
            </Col>
            <Col span='7'>
            <div class="panel content">
                <div class="panel-header">
                    <div class="title-text">城市分布分析</div>
                </div>
                <div class="appUse">
                    <!-- 城市分布分析 -->
                    <cityBar ref="chart7" :data="cityData"></cityBar>
                </div>
            </div>
            </Col>
        </Row>

        <!-- 加载和错误提示 -->
        <div v-if="isLoading" class="data-loading">
            <div class="loading-spinner"></div>
            <p>数据加载中...</p>
        </div>

        <div v-if="dataError && !isLoading" class="data-error">
            <div class="error-icon">!</div>
            <p>{{ dataError }}</p>
            <button @click="loadJobData" class="retry-button">重试</button>
        </div>
    </div>
</template>

<script>
// 导入数据处理工具
import { processJobData } from '@/utils/dataProcessor';

// 保留原有的有用组件
const chart2 = () => import('./components/page1/chart2');
const chart4 = () => import('./components/page1/chart4');
const chart6 = () => import('./components/page1/chart6');
const pie = () => import('./components/pie');
const cityBar = () => import('./components/page1/cityBar');

// 新增的组件
const salaryTrend = () => import('./components/page1/salaryTrend');
const jobProfileCenter = () => import('./components/page1/jobProfileCenter');
const jobRadar = () => import('./components/page1/jobRadar');
const salaryBox = () => import('./components/page1/salaryBox');
const jobFunnel = () => import('./components/page1/jobFunnel');
const jobProfileDetails = () => import('./components/page1/jobProfileDetails');

// 引入职位聚类概览图组件
const jobClustering = () => import('./components/page1/jobClustering');

export default {
    name: 'page1',
    props: {
        selectRangeDate: Array
    },
    components: {
        chart2,
        chart4,
        chart6,
        pie,
        cityBar,
        salaryTrend,
        jobProfileCenter,
        jobRadar,
        salaryBox,
        jobFunnel,
        jobProfileDetails,
        jobClustering
    },
    data() {
        return {
            // 各种数据定义，初始化为空对象或空数组
            salaryTrendData: {},
            industryData: {},
            salaryData: {},
            experienceData: {},
            educationData: {},
            cityData: {},
            jobProfiles: [],
            jobDifferenceData: {},
            salaryRangeData: {},
            companyTypeData: {},
            salaryBoxData: {},
            salaryDetail1: {},
            salaryDetail2: {},
            hotJobsData: [],
            jobClusterData: {},
            jobDemandData: [],
            jobProfileDetailsData: {},
            isLoading: false,
            dataError: null,
            resizeFn: null
        }
    },
    methods: {
        // 使用dataProcessor加载数据
        async loadJobData() {
            this.isLoading = true;
            this.dataError = null;

            try {
                console.log('开始加载职位数据...');
                // 调用dataProcessor中的方法处理数据
                const result = await processJobData();

                if (!result) {
                    throw new Error('数据处理返回空结果');
                }

                console.log('数据处理完成，准备更新界面');
                // 将处理后的数据转换为图表所需格式
                this.formatDataForCharts(result);

            } catch (error) {
                console.error('数据加载或处理失败:', error);
                this.dataError = error.message || '数据加载失败';
                // 不再生成模拟数据，直接展示错误信息
            } finally {
                this.isLoading = false;
                // 刷新图表
                this.$nextTick(() => {
                    this.refreshCharts();
                });
            }
        },

        // 将处理后的数据格式化为各图表需要的格式
        // 将处理后的数据格式化为各图表需要的格式
        formatDataForCharts(processedData) {
            const {
                industryAnalysis,
                salaryAnalysis,
                experienceAnalysis,
                educationAnalysis,
                cityAnalysis,
                jobProfiles,
                jobDifferenceData,
                jobSalaryComparisonData,
                salaryDetailData
            } = processedData;

            // 1. 行业类别分析
            this.industryData = {
                name: '行业类别TOP5',
                number: industryAnalysis.length + '个',
                data: industryAnalysis.slice(0, 5).map(item => item.name),
                color: '192,35,42',
                value: industryAnalysis.slice(0, 5).map(item => item.value)
            };

            // 设置公司类型分析数据
            this.companyTypeData = {
                title: '主要行业类别',
                data: industryAnalysis.slice(0, 6).map((item, index) => ({
                    value: item.value,
                    name: item.name,
                    itemStyle: {
                        color: ['#a262f2', '#2ca8fe', '#feac2c', '#c0232a', '#2cd9fe', '#252448'][index]
                    }
                }))
            };

            // 2. 薪资分析
            // 转换薪资数据格式以适配图表
            const salaryRanges = {
                '5K以下': 0,
                '5-10K': 0,
                '10-15K': 0,
                '15-20K': 0,
                '20-30K': 0,
                '30K以上': 0
            };

            // 将数据处理器返回的薪资数据映射到我们的区间
            salaryAnalysis.forEach(item => {
                if (item.name === '0-5K') salaryRanges['5K以下'] = item.value;
                else if (item.name === '5-10K') salaryRanges['5-10K'] = item.value;
                else if (item.name === '10-15K') salaryRanges['10-15K'] = item.value;
                else if (item.name === '15-20K') salaryRanges['15-20K'] = item.value;
                else if (item.name === '20K+') {
                    // 将20K+区间拆分为两个区间
                    salaryRanges['20-30K'] = Math.round(item.value * 0.7);
                    salaryRanges['30K以上'] = Math.round(item.value * 0.3);
                }
            });

            // 设置薪资分布数据
            this.salaryData = {
                title: "薪资分布",
                color: '#BE232A',
                data: Object.entries(salaryRanges).map(([name, value], index) => ({
                    value,
                    name,
                    itemStyle: {
                        color: ['#a262f2', '#2ca8fe', '#feac2c', '#c0232a', '#2cd9fe', '#fe672c'][index]
                    }
                }))
            };

            // 设置薪资趋势数据
            this.salaryTrendData = {
                ranges: Object.keys(salaryRanges),
                values: Object.values(salaryRanges)
            };

            // 设置薪资范围分析数据
            this.salaryRangeData = {
                title: '薪资范围分析',
                data: Object.entries(salaryRanges).map(([name, value], index) => ({
                    value,
                    name,
                    itemStyle: {
                        color: ['#252448', '#2ca8fe', '#feed2c', '#2871ea', '#fe672c', '#a262f2'][index]
                    }
                }))
            };

            // 3. 经验要求分析
            this.experienceData = {
                name: '经验要求TOP5',
                number: experienceAnalysis.length + '种',
                data: experienceAnalysis.slice(0, 5).map(item => item.name),
                color: '40,112,232',
                value: experienceAnalysis.slice(0, 5).map(item => item.value)
            };

            // 4. 学历要求分析
            this.educationData = {
                title: "学历要求分布",
                color: '#2C7BFE',
                data: educationAnalysis.slice(0, 6).map((item, index) => ({
                    value: item.value,
                    name: item.name,
                    itemStyle: {
                        color: ['#a262f2', '#2ca8fe', '#feac2c', '#c0232a', '#2cd9fe', '#252448'][index]
                    }
                }))
            };

            // 5. 城市分布分析
            this.cityData = {
                name: '城市分布TOP10',
                number: cityAnalysis.length + '个',
                data: cityAnalysis.slice(0, 10).map(item => item.name),
                color: '44,168,254',
                value: cityAnalysis.slice(0, 10).map(item => item.value)
            };

            // 6. 职位画像
            this.jobProfiles = jobProfiles;

            // 7. 职位差异度分析
            this.jobDifferenceData = jobDifferenceData || this.calculateJobDifferenceFromData(processedData);

            // 8. 职位聚类分析（使用空对象，由组件内部生成可视化数据）
            this.jobClusterData = {};

            // 9. 职位需求分析
            this.jobDemandData = this.analyzeJobDemandFromData(processedData);

            // 10. 职位画像详细数据
            const totalJobs = this.getTotalJobsCount(processedData);
            let average = '0.0';

            // 计算平均薪资
            if (salaryAnalysis && salaryAnalysis.length > 0) {
                let totalSalaryWeight = 0;
                let totalWeight = 0;

                salaryAnalysis.forEach(item => {
                    const salary = this.estimateSalaryValue(item.name);
                    totalSalaryWeight += salary * item.value;
                    totalWeight += item.value;
                });

                average = totalWeight > 0 ? (totalSalaryWeight / totalWeight).toFixed(1) : '0.0';
            }

            this.jobProfileDetailsData = {
                profiles: jobProfiles.slice(0, 5),
                totalJobs: totalJobs,
                avgSalary: average + 'K',
                topIndustries: industryAnalysis.slice(0, 5),
                topCities: cityAnalysis.slice(0, 5)
            };

            // 11. 薪资箱线图数据
            this.salaryBoxData = {
                jobProfiles: jobProfiles, // 直接传递所有职位画像数据供组件使用
                average: average // 保留平均薪资信息
            };

            // 12. 薪资详细分析 - 从真实数据获取
            if (salaryDetailData) {
                this.salaryDetail1 = salaryDetailData.highSalaryDetail;
                this.salaryDetail2 = salaryDetailData.lowSalaryDetail;
            } else {
                // 生成默认的薪资详情数据
                this.generateSalaryDetailsFromData(processedData);
            }
        },

        // 从原始数据估算各个薪资区间的代表值
        estimateSalaryValue(salaryRange) {
            switch (salaryRange) {
                case '0-5K': return 3;
                case '5-10K': return 7.5;
                case '10-15K': return 12.5;
                case '15-20K': return 17.5;
                case '20K+': return 25;
                default: return 0;
            }
        },

        // 计算职位差异度数据
        calculateJobDifferenceFromData(processedData) {
            // 从处理后的数据中提取差异度指标
            // 这里可以基于实际分析结果生成更精确的差异度评分

            const dimensions = ['薪资差异', '经验差异', '学历差异', '行业差异', '地域差异'];

            // 计算各维度的差异度分数
            // 这里使用一个简单的计算逻辑，实际项目中可能需要更复杂的算法
            const salaryDiff = this.calculateDiversity(processedData.salaryAnalysis);
            const expDiff = this.calculateDiversity(processedData.experienceAnalysis);
            const eduDiff = this.calculateDiversity(processedData.educationAnalysis);
            const indDiff = this.calculateDiversity(processedData.industryAnalysis);
            const cityDiff = this.calculateDiversity(processedData.cityAnalysis);

            return {
                dimensions,
                scores: [salaryDiff, expDiff, eduDiff, indDiff, cityDiff]
            };
        },

        // 计算数据的多样性/差异度
        calculateDiversity(data) {
            if (!data || !data.length) return 50; // 默认值

            // 使用基尼系数的简化计算来表示多样性
            const total = data.reduce((sum, item) => sum + item.value, 0);
            if (total === 0) return 50;

            // 计算每项的占比
            const proportions = data.map(item => item.value / total);

            // 计算熵值来表示多样性
            const entropy = proportions.reduce((sum, p) => {
                if (p === 0) return sum;
                return sum - p * Math.log(p);
            }, 0);

            // 归一化到0-100
            const maxEntropy = Math.log(data.length);
            const normalizedDiversity = maxEntropy > 0 ? (entropy / maxEntropy) * 100 : 50;

            // 确保值在合理范围内
            return Math.min(100, Math.max(40, normalizedDiversity));
        },

        // 分析职位需求数据
        analyzeJobDemandFromData(processedData) {
            // 从原始数据中提取职位需求指标
            const totalJobs = this.getTotalJobsCount(processedData);

            // 分析薪资数据获取高薪职位数量
            const highSalaryJobs = this.countHighSalaryJobs(processedData.salaryAnalysis);

            // 使用合理的比例计算其他指标
            const hotJobs = Math.round(totalJobs * 0.7);
            const scarceJobs = Math.round(totalJobs * 0.4);
            const qualityJobs = Math.round(totalJobs * 0.2);

            return [
                { name: '总职位数', value: totalJobs },
                { name: '高薪职位', value: highSalaryJobs },
                { name: '热门职位', value: hotJobs },
                { name: '紧缺职位', value: scarceJobs },
                { name: '优质职位', value: qualityJobs }
            ];
        },

        // 计算总职位数
        getTotalJobsCount(processedData) {
            // 如果industryAnalysis包含了所有职位，可以从这里获取总数
            return processedData.industryAnalysis.reduce((sum, item) => sum + item.value, 0);
        },

        // 计算高薪职位数量（15K以上）
        countHighSalaryJobs(salaryAnalysis) {
            return salaryAnalysis
                .filter(item => item.name === '15-20K' || item.name === '20K+')
                .reduce((sum, item) => sum + item.value, 0);
        },

        // 生成薪资详细分析数据
        generateSalaryDetailsFromData(processedData) {
            // 从原始数据中提取薪资分布数据
            const totalJobs = this.getTotalJobsCount(processedData);

            // 高薪职位（15K以上）
            const highSalaryJobs = this.countHighSalaryJobs(processedData.salaryAnalysis);

            // 低薪职位（8K以下）
            const lowSalaryJobs = processedData.salaryAnalysis
                .filter(item => item.name === '0-5K' || (item.name === '5-10K' && item.value > 0))
                .reduce((sum, item) => {
                    // 对于5-10K区间，只计算一部分作为低薪（假设5-8K占60%）
                    if (item.name === '5-10K') {
                        return sum + Math.round(item.value * 0.6);
                    }
                    return sum + item.value;
                }, 0);

            // 设置高薪职位分布数据
            this.salaryDetail1 = {
                title: '高薪职位分布',
                data: [
                    { value: highSalaryJobs, name: '高薪职位', itemStyle: { color: '#c0232a' } },
                    { value: totalJobs - highSalaryJobs, name: '其他职位', itemStyle: { color: '#2870e8' } },
                ],
                data1: [
                    { value: Math.floor(highSalaryJobs * 0.4), name: '30K以上', itemStyle: { color: '#c0232a' } },
                    { value: Math.floor(highSalaryJobs * 0.3), name: '20-30K', itemStyle: { color: '#2870e8' } },
                    { value: Math.floor(highSalaryJobs * 0.2), name: '15-20K', itemStyle: { color: '#feed2c' } },
                    { value: Math.floor(highSalaryJobs * 0.1), name: '其他', itemStyle: { color: '#a262f2' } }
                ]
            };

            // 设置低薪职位分布数据
            this.salaryDetail2 = {
                title: '低薪职位分布',
                data: [
                    { value: lowSalaryJobs, name: '低薪职位', itemStyle: { color: '#c0232a' } },
                    { value: totalJobs - lowSalaryJobs, name: '其他职位', itemStyle: { color: '#2870e8' } },
                ],
                data1: [
                    { value: Math.floor(lowSalaryJobs * 0.4), name: '5K以下', itemStyle: { color: '#c2232a' } },
                    { value: Math.floor(lowSalaryJobs * 0.3), name: '5-8K', itemStyle: { color: '#fe672c' } },
                    { value: Math.floor(lowSalaryJobs * 0.2), name: '8-10K', itemStyle: { color: '#a262f2' } },
                    { value: Math.floor(lowSalaryJobs * 0.1), name: '其他', itemStyle: { color: '#2870e8' } }
                ]
            };
        },

        // 刷新所有图表
        refreshCharts() {
            // 刷新所有图表
            for (let i = 1; i <= 17; i++) {
                if (this.$refs['chart' + i] && this.$refs['chart' + i].setChart) {
                    this.$refs['chart' + i].setChart();
                }
            }
        },

        // Add this method to the page1.vue file's methods section
        generateJobSalaryBoxplotData(jobProfiles) {
            if (!jobProfiles || !jobProfiles.length) return null;

            // Sort job profiles by count (most jobs first)
            const sortedProfiles = [...jobProfiles].sort((a, b) => b.count - a.count);
            const topJobs = sortedProfiles.slice(0, 5); // Get top 5 jobs by count

            const jobTitles = topJobs.map(job => job.title);
            const boxplotData = topJobs.map(job => this.calculateSalaryBoxplot(job.salaries));

            return {
                jobTitles,
                boxplotData
            };
        },

        // Calculate box plot data points for a job's salary array
        calculateSalaryBoxplot(salaries) {
            if (!salaries || !salaries.length) return [0, 0, 0, 0, 0];

            // Parse salary strings to numbers (average of range)
            const salaryValues = [];

            salaries.forEach(salary => {
                const salaryStr = String(salary);
                const match = salaryStr.match(/(\d+)(?:k|K)?(?:\s*[-~]\s*)(\d+)?(?:k|K)?/i);
                if (match) {
                    const min = parseInt(match[1]);
                    const max = match[2] ? parseInt(match[2]) : min;
                    salaryValues.push((min + max) / 2);
                }
            });

            if (salaryValues.length === 0) return [0, 0, 0, 0, 0];

            // Sort values to calculate percentiles
            salaryValues.sort((a, b) => a - b);

            const length = salaryValues.length;
            const min = salaryValues[0];
            const max = salaryValues[length - 1];

            // Calculate quartiles
            const q1Index = Math.floor(length * 0.25);
            const q2Index = Math.floor(length * 0.5);
            const q3Index = Math.floor(length * 0.75);

            const q1 = length > 1 ? salaryValues[q1Index] : min;
            const median = length > 1 ? salaryValues[q2Index] : min;
            const q3 = length > 1 ? salaryValues[q3Index] : max;

            return [min, q1, median, q3, max];
        },
    },
    mounted() {
        // 加载数据
        this.loadJobData().catch(error => {
            console.error('加载数据失败:', error);
        });

        // 窗口大小变化时刷新图表
        this.resizeFn = this.$debounce(() => {
            try {
                for (let i = 1; i <= 17; i++) {
                    if (this.$refs['chart' + i] && this.$refs['chart' + i].setChart) {
                        this.$refs['chart' + i].setChart();
                    }
                }
            } catch (error) {
                console.error('图表刷新出错:', error);
            }
        }, 300);

        window.addEventListener('resize', this.resizeFn);
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.resizeFn);
    }
}
</script>

